<template>
    <!-- bg-gradient-primary -->
    <div class="card tk-bg-gradient-3">
        <!-- Card body -->
        <div class="card-body">
            <div class="row justify-content-between align-items-center">
                <div class="col">
                    <img src="/img/icons/cards/visa.png" v-if="type === 0">
                    <img src="/img/icons/cards/mastercard.png" v-if="type === 1">
                </div>
                <!-- <div class="col-auto">
                    <span class="badge badge-lg" :class="`badge-${creditCard.statusType}`">{{creditCard.status}}</span>
                </div> -->
                <div class="col-auto">
                  <i class="fas fa-trash tk-font-color-white tk-card-unbind-btn" @click.prevent="deletePaymentMethod"></i>
                </div>
            </div>
            <div class="my-4">
                <span class="h6 surtitle text-light">
                  Card number
                </span>
                <div class="card-serial-number h1 text-white">
                    <div v-for="(part, index) in creditCard.number.split(' ')" :key="index">
                      {{part}}
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col">
                    <span class="h6 surtitle text-light">Name</span>
                    <span class="d-block h3 text-white">{{creditCard.name}}</span>
                </div>
                <div class="col">
                    <span class="h6 surtitle text-light">Expiry date</span>
                    <span class="d-block h3 text-white">{{creditCard.expire}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
  export default {
    name: 'visa-card',
    props: ['type'],
    data() {
      return {
        creditCard: {
          number: '4358 7421 9256 6682',
          status: 'Active',
          statusType: 'success',
          name: 'John Snow',
          expire: '11/23',
        },
        gradientClass: ""
      }
    },
    mounted() {
      let self = this
      self.gradientClass = self.getRandomGradient(0,6);
    },
    methods: {
      getRandomGradient(min, max) {
        let aNumber = (max - min) * Math.random() + (min + 1)
        let result = Math.floor(aNumber)
        let className = "tk-bg-gradient-" + result
        // console.log(className)
        return className
      },
      deletePaymentMethod() {
        this.$emit("deletePaymentMethod")
      }
    }
  }
</script>
